@import "../../styleVariables/colors.css";

.App {
  display: flex;
  flex-direction: column;

  text-align: center;
  max-height: 100vh;

  background-color: var(--color-background-global);
}

.container {
  display: flex;
  flex-grow: 1;

  height: calc(100vh - 120px);
  max-width: 1280px;
  width: 100vw;

  margin: 0 auto;
}
